<template>
  <div class="container">
    <search-bar @showFlapCard="showFlapCard"></search-bar>
    <scroll
      :top="94"
      @onScroll="onScroll"
      ref="scroll"
    >
      <div class="banner-wrapper">
        <!-- 
        <div
          class="banner-img"
          :style="{backgroundImage:`url(${banner})`}"
        ></div> -->

        <div class="banner-img"></div>

      </div>
      <guess-you-like :data="guessYouLike"></guess-you-like>

      <featured
        :data="featured"
        :titleText="$t('home.featured')"
        :btnText="$t('home.seeAll')"
        class="featured"
      ></featured>
      <recommend :data="recommend"></recommend>
      <div
        class="category-list-wrapper"
        v-for="(item,index) in categoryList"
        :key="index"
      >
        <category-book
          :data="item"
          :idx="index"
        />
      </div>

      <category
        class="categories"
        :data="categories"
      />
    </scroll>
    <flap-card
      v-if="ifFlapCardShow"
      @close="closeFlapCard"
      :data="random"
      ref="flapCard"
    ></flap-card>
  </div>
</template>

<script>
import SearchBar from '../../components/home/searchBar'
import GuessYouLike from '../../components/home/guessYouLike'
import Featured from '../../components/home/featured'
import CategoryBook from '../../components/home/categoryBook'
import Category from '../../components/home/category'
import FlapCard from '@/components/home/flapCard'

import Scroll from '../../components/common/Scroll'
import Recommend from '../../components/home/recommend'
import { storeHomeMixin } from '../../utils/mixin'
import { home } from '../../api/store'

export default {
  name: 'home',
  mixins: [storeHomeMixin],
  components: {
    SearchBar,
    Scroll,
    GuessYouLike,
    Recommend,
    Featured,
    CategoryBook,
    Category,
    FlapCard
  },

  data () {
    return {
      scrollTop: 94,
      random: null,
      banner: null,
      guessYouLike: null,
      recommend: null,
      featured: null,
      categoryList: null,
      categories: null,
      ifFlapCardShow: false,
      randomList: null,
    }
  },
  mounted () {
    home().then(res => {

      if (res && res.status === 200) {
        const data = res.data
        const randomIndex = Math.floor(Math.random() * data.random.length)
        this.random = data.random[randomIndex]
        this.banner = data.banner
        this.guessYouLike = data.guessYouLike
        this.recommend = data.recommend
        this.featured = data.featured
        this.categoryList = data.categoryList
        this.categories = data.categories
        this.randomList = data.random
      }
    })
  },
  methods: {
    onScroll (offsetY) {
      this.setOffsetY(offsetY)
      this.scrollTop = offsetY > 0 ? 52 : 94
      this.$refs.scroll.refresh()
    },
    showFlapCard () {
      this.ifFlapCardShow = true
      const randomNumber = parseInt(Math.random() * this.randomList.length)
      this.random = this.randomList[randomNumber]
      this.$nextTick(() => {
        this.$refs.flapCard.startAnimation()
      })
    },
    closeFlapCard () {
      this.$refs.flapCard.stopAnimation()
      this.ifFlapCardShow = false
    },
  }
}
</script>

<style lang='scss' rel='stylesheet/scss' scoped>
@import "../../assets/styles/global";
.container {
  width: 100%;
  height: 100%;
  background-color: #fff;
  .banner-wrapper {
    width: 100%;
    padding: px2rem(10);
    box-sizing: border-box;
    .banner-img {
      width: 100%;
      height: px2rem(150);
      border-radius: px2rem(5);
      background-image: url("data:image/jpeg;base64,");
      background-repeat: no-repeat;
      background-size: 100% 100%;
    }
  }
  .recommend {
    // margin-top: px2rem(20);
    padding: px2rem(10) 0;
  }
  .featured {
    // margin-top: px2rem(20);
    padding: px2rem(10) 0;
  }
  .category-list-wrapper {
    // margin-top: px2rem(20);
  }
  .categories {
    margin-top: px2rem(20);
  }
}
</style>
