<template>

  <div>

    <div
      :class="{'hide-title':!titleVisible,'title-shadow':isShowShadow}"
      class="search-bar-wrapper"
    >
      <transition name="title-move">
        <div
          class="search-bar-title-wrapper"
          v-show="titleVisible"
        >

          <!-- 书城标题 -->
          <div class="title-text-wrapper">
            <span class="title-text title">{{$t('home.title')}}</span>
          </div>
          <!-- 随机推荐 -->
          <div
            class="title-icon-shake-wrapper"
            @click="showFlapCard"
          >
            <span class="icon-shake icon"></span>
          </div>
        </div>

      </transition>

      <!-- 返回图标 -->
      <div
        class="title-icon-back-wrapper"
        @click="back"
        :class="{'hide-title':!titleVisible}"
      >
        <span class="icon-back icon"></span>
      </div>

      <!-- 搜索框 -->
      <div
        class="search-input-wrapper"
        :class="{'hide-title':!titleVisible}"
      >
        <div
          class="search-bar-blank"
          :class="{'hide-title':!titleVisible}"
        ></div>
        <div class="search-bar-input">

          <span class="icon-search icon"></span>
          <input
            class="input"
            type="text"
            :placeholder="$t('home.hint')"
            v-model="searchText"
            @click="showHotSearch"
            @keyup.13.exact="search"
          >
        </div>
      </div>
    </div>
    <hot-search-list
      v-show="hotSearchVisible"
      ref="hotSearch"
    ></hot-search-list>
  </div>
</template>

<script type="text/ecmascript-6">
// import { realPx } from '@/utils/utils'
import HotSearchList from '@/components/home/HotSearchList'
import { storeHomeMixin } from '../../utils/mixin'
export default {
  mixins: [storeHomeMixin],
  components: {
    HotSearchList
  },
  data () {
    return {
      searchText: '',
      titleVisible: true,
      isShowShadow: false,
      hotSearchVisible: false
    }
  },
  methods: {
    showTitle () {
      this.titleVisible = true
    },
    hideTitle () {
      this.titleVisible = false
    },
    showShadow () {
      this.isShowShadow = true
    },
    hideShadow () {
      this.isShowShadow = false
    },

    // 显示隐藏热门搜索
    showHotSearch () {
      this.hideTitle()
      this.showShadow()
      this.hotSearchVisible = true

      // 归位
      this.$nextTick(() => {
        this.$refs.hotSearch.reset()
      })
    },
    hideHotSearch () {
      this.hotSearchVisible = false
    },
    // 返回
    back () {
      if (!this.titleVisible) {
        this.hideHotSearch()
        this.showTitle()
      } else {
        this.hideHotSearch()
        this.showTitle()
        this.$router.go(-1)
      } 0
    },
    showFlapCard () {
      this.$emit('showFlapCard')
    },
    // 搜索
    search () {

      this.$router.push({
        path: '/store/list',
        query: {
          keyword: this.searchText
        }
      })
    }


  },
  watch: {
    offsetY (val) {
      if (val > 0) {
        this.hideTitle()
        this.showShadow()
      } else {
        this.showTitle()
        this.hideShadow()
      }

    },
    hotSearchOffsetY (val) {
      if (val > 0) {
        this.showShadow()
      } else {
        this.hideShadow()
      }
    }
  }
}
</script>

