<template>
  <div
    class="category-book"
    :class="{grey:idx%2==0}"
  >
    <title-view
      :label="categoryText(data.category)"
    ></title-view>
    <div class="category-book-list">
      <div
        class="category-book-item"
        v-for="(item, index) in data.list"
        :key="index"
        @click="showBookDetail(item)"
      >
        <div class="img-wrapper">
          <img
            class="img"
            v-lazy="item.cover"
          >
        </div>
        <div class="content-wrapper">
          <div
            class="title title-small"
            ref="title"
          >{{item.title}}</div>
          <div
            class="num sub-title-tiny"
            ref="author"
          >{{item.author}}</div>
        </div>
      </div>
    </div>
    <div class="btn-wrapper">
      <div
        class="btn"
        :class="{'touch': ifOnTouch}"
        @touchstart="onTouchStart"
        @touchend="onTouchEnd"
        @mousedown="onTouchStart"
        @mouseup="onTouchEnd"
        @click="click"
      >{{$t('home.seeAll')}}</div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import TitleView from '@/components/home/title'
import { categoryText, getCategoryName } from '@/utils/book'
import { storeHomeMixin } from '../../utils/mixin'

export default {
  mixins: [storeHomeMixin],
  components: {
    TitleView
  },
  props: {
    data: Object,
    idx: Number
  },
  data () {
    return {
      ifOnTouch: false
    }
  },
  methods: {
    showBookCategory () {

      this.$router.push({
        path: '/store/list',
        query: {
          category: getCategoryName(this.data.category),
          categoryText: this.categoryText(this.data.category)
        }
      })
    },
    categoryText (category) {
      return categoryText(category, this)
    },
    onTouchStart () {
      this.ifOnTouch = true
    },
    onTouchEnd () {
      this.ifOnTouch = false
    },
    click () {
      this.showBookCategory()
    }
  }
}
</script>

